import Button from 'react-bootstrap/Button';
import { Col } from 'react-bootstrap';

export default function EatComponent({ onClick, onClick2 }) {

    return (
        <Col sm={8} md={9} className="mx-auto">
            <div className="mx-2 my-2 mb-4 ">
                <div className="card-container px-2 py-2">
                    <h2 className="header-title text-uppercase mt-4">EAT</h2>
                    <img
                        className="d-block w-100 custom-rounded custom-height col-md-8 offset-md-2"
                        src={"./restLogo.png"}
                        alt={"logo"}

                    />
                    <p className="text-center mx-2  d-block" style={{ fontSize: "0.95rem" }}>
                        Located on the ground floor of the hotel. The bakery shop offers
                        a wide range of French croissanterie and breads on dine in or
                        takeout basis. The all day fine casual dining bistro opens for daily
                        breakfast, lunch and dinner offering wide variety of dishes from around
                        the world.
                    </p>
                    <p className="my-2 d-block menuLink" onClick={onClick}>Click Here for menu</p>

                    {               /*     <div className="d-block my-1 my-xl-4">

                        <Button className="w-25 my-4 text-uppercase" style={{ backgroundColor: "rgb(69,69,69,0.9)", fontWeight: "bold" }} onClick={onClick}>Menu</Button>
    </div> */}




                    <h5 style={{ fontSize: "1rem" }}>In room dinning</h5>
                    <p className="mx-2 pb-lg-3 pb-xl-1 d-block">Now, safe and fresh in room dining experience is
                available for our in house guests from 9 am - 9 pm</p>

                    <p className="my-2 d-block menuLink" onClick={onClick2} >Click Here for menu</p>

                </div>

            </div>
        </Col>
    );
}